@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
}

.page__body {
  padding: 0;
  font-family: "PTSans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  background-color: #e7e5e5;
}

.wrapper {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  position: relative;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@media (min-width: 768px) {
  .wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 1200px;
  }
}
.visually-hidden {
  position: absolute;
  border: 0;
  padding: 0;
  display: none;
}

.page-titles {
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

