.main-header {
  position: relative;
}

.main-header__logo {
  position: absolute;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
}

.main-header__logo-image {
  text-align: center;
}

.nav {
  width: 320px;
  min-height: 56px;
  position: relative;
}

.nav__list {
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  text-align: center;
  padding-top: 17px;
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #80b2d1;
}

.nav__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

.nav__button {
  border: none;
  background-color: transparent;
  position: absolute;
  top: 16px;
  right: 14px;
  z-index: 2;
  cursor: pointer;
}

.nav__button-close {
  fill: #ffffff;
}
.nav__button-close:hover {
  fill: rgba(225, 225, 225, 0.6);
}
.nav__button-close:active {
  fill: rgba(225, 225, 225, 0.3);
}

.nav__button-open {
  fill: #81b3d2;
}
.nav__button-open:hover {
  fill: rgba(129, 179, 210, 0.6);
}
.nav__button-open:active {
  fill: rgba(129, 179, 210, 0.3);
}

@media (max-width: 767px) {
  .nav__list {
    display: none;
  }

  .nav__item {
    display: block;
  }
  .nav__item:hover {
    background-color: #669ec0;
  }
  .nav__item:active {
    background-color: #5496bd;
  }
  .nav__item:active .nav__link {
    color: rgba(225, 225, 225, 0.3);
  }

  .nav--nojs .nav__list {
    display: block;
  }

  .nav--nojs .main-header__logo {
    display: block;
    width: 185px;
    margin: 0 auto;
    position: static;
    left: unset;
    transform: unset;
  }

  .nav__menu--opened .nav__list {
    position: absolute;
    display: flex;
    width: 320px;
  }

  .nav__menu--opened .nav__button--close {
    display: block;
  }

  .nav__menu--opened .main-header__logo {
    display: none;
  }

  .nav__menu--opened .nav__button--open {
    display: none;
  }

  .nav--nojs .nav__toggle {
    display: none;
  }

  .nav--nojs .nav__button--close {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav {
    position: relative;
    width: 768px;
  }

  .nav__button {
    display: none;
  }

  .nav__list {
    display: flex;
    flex-direction: row;
    background-color: #ffffff;
  }

  .nav__item {
    width: 20%;
    border: none;
    padding: 18px 0 17px;
    background-color: unset;
  }

  .nav__link {
    font-size: 14px;
    color: #000000;
    line-height: 21px;
  }
  .nav__link--current {
    color: #766357;
  }
  .nav__link:hover:not(.nav__link--current) {
    color: #81b3d2;
  }
  .nav__link:active:not(.nav__link--current) {
    color: rgba(0, 0, 0, 0.3);
  }

  .nav__item:nth-child(2n):not(:nth-child(4n)) {
    margin-right: 20%;
  }

  .nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .nav {
    width: 1200px;
  }
}
