  background-image: url("/img/phone.svg");
  background-repeat: no-repeat;
}
.form__icon--mail {
  background-image: url("/img/mail.svg");
  background-repeat: no-repeat;
}

.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__item {
  display: flex;
  flex-direction: column;
}

.form__item:not(:last-child) {
  margin-bottom: 16px;
}

.form__selector:not(:last-child) {
  margin-bottom: 16px;
}

.form__selector-label {
  display: flex;
  align-items: center;
  font-family: "PTSans", sans-serif;
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: none;
}

.form__selector-custom {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  margin-right: 16px;
}
.form__selector-custom:hover {
  background-color: #e5e5e5;
}
.form__selector-custom:active {
  background-color: #d5d5d5;
}

.form__selector-indicator {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.form__selector-custom::before {
  content: "";
  display: none;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.form__selector-indicator:checked + .form__selector-custom::before {
  display: block;
}

.form__selector-custom:hover::before {
  background-color: rgba(51, 51, 51, 0.3);
}

.form__checkbox-custom {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
  margin-right: 16px;
}
.form__checkbox-custom:hover {
  background-color: #e5e5e5;
}

.form__checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;
